import React, { Component } from 'react'
import G6 from '../register';
import styles from './index.module.scss';



export default class index extends Component {

    componentDidMount() {
        this.init()
    }

    init() {
        const data = {
            nodes: [{
                id: '0',
                label: '0',
                text: '\ue674'
            },
            {
                id: '1',
                label: '1',
                text: '\ue674'
            },
            {
                id: '2',
                label: '2',
                text: '\ue674'
            },
            {
                id: '3',
                label: '3',
                text: '\ue674'
            },
            {
                id: '4',
                label: '4',
                text: '\ue674'
            },
            {
                id: '5',
                label: '5',
                text: '\ue674'
            },
            {
                id: '6',
                label: '6',
                text: '\ue674'
            },
            {
                id: '7',
                label: '7',
                text: '\ue674'
            },
            {
                id: '8',
                label: '8',
                text: '\ue674'
            },
            {
                id: '9',
                label: '9',
                text: '\ue674'
            }],
            edges: [{
                source: '0',
                target: '1'
            },
            {
                source: '0',
                target: '2'
            },
            {
                source: '0',
                target: '3'
            },
            {
                source: '0',
                target: '4'
            },
            {
                source: '0',
                target: '5'
            },
            {
                source: '0',
                target: '7'
            },
            {
                source: '0',
                target: '8'
            },
            {
                source: '0',
                target: '9'
            },
            {
                source: '2',
                target: '3'
            },
            {
                source: '4',
                target: '5'
            },
            {
                source: '4',
                target: '6'
            },
            {
                source: '5',
                target: '6'
            }]
        };
        const COLOR = '#40a9ff';
        const dom = document.getElementById('container') as HTMLElement;
        const width = dom.scrollWidth;
        const height = dom.scrollHeight || 500;
        const graph = new G6.Graph({
            container: 'container',
            width,
            height,
            layout: {
                type: 'fruchterman',
                gravity: 5,
                speed: 5
            },
            modes: {
                default: ['drag-node']
            },
            defaultNode: {
                size: 20,
                color: '#5B8FF9',
                style: {
                    lineWidth: 2,
                    fill: '#C6E5FF'
                },
                type: 'iconfont',
                labelCfg: {
                    style: {
                        fill: COLOR,
                        fontSize: 12,
                    },
                },
                backgroundConfig:{
                    fill:'Coral'
                }
            },
            defaultEdge: {
                size: 1,
                color: '#e2e2e2'
            }
            
        });
        graph.data(data);
        graph.render();

        function refreshDragedNodePosition(e: any) {
            const model = e.item.get('model');
            model.fx = e.x;
            model.fy = e.y;
        }
        graph.on('node:dragstart', (e: any) => {
            graph.layout();
            refreshDragedNodePosition(e);
        });
        graph.on('node:drag', (e: any) => {
            refreshDragedNodePosition(e);
        });
    }

    render() {
        return (
            <div id="container" className={styles.shapCont}>

            </div>
        )
    }
}

